  width: 100%;
  height: auto;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.fashion-news__text {
  font-size: clamp(1rem, 0.898rem + 0.44vw, 1.25rem);
  line-height: 1;
  color: var(--main-color);
  margin-top: 1.25rem;
}
.fashion-news__text strong {
  display: inline-block;
  margin-bottom: 10px;
}
@media screen and (width < 809.99px) {
  .fashion-news .swiper-pag {
    display: flex;
    justify-content: center;
    gap: 8px;
    position: relative;
    bottom: 0;
  }
}
.fashion-news .swiper-pagination-bullet {
  background-color: var(--main-color);
  width: 1.5rem;
  height: 0.125rem;
}
.fashion-news .swiper {
  width: 100%;
}
.fashion-news .grid {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
}
.fashion-news .swiper-slide {
  margin: 0 auto;
}
@media (max-width: 1250px) {
  .fashion-news .slider-1 .swiper-wrapper {
    display: flex;
    gap: 0;
  }
  .fashion-news .swiper__control {
    display: flex;
    bottom: 0;
  }
}

.footer {
  padding-top: clamp(3.75rem, 3.238rem + 2.19vw, 5rem);
  padding-bottom: 1.5rem;
  background-color: #f6f6f6;
  text-align: center;
}
.footer__inner {
  max-width: 55.4375rem;
  display: flex;
  gap: 1.875rem;
  justify-content: space-between;
  align-items: flex-start;
  margin-right: auto;
  margin-left: auto;
  margin-bottom: clamp(1.875rem, 1.121rem + 3.26vw, 3.75rem);
}
.footer__info {
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 1.875rem;
  font-size: 0.875rem;
  line-height: 1;
  text-transform: uppercase;
  color: var(--black);
}
.footer__title {
  font-weight: 600;
  pointer-events: none;
  transition-duration: 550ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
.footer__title-drop {
  display: none;
}
.footer__item:not(:last-child) {
  margin-bottom: 1.25rem;
}
.footer__link {
  color: inherit;
}
.footer__link--red {
  color: var(--red);
}
.footer__render {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.625rem;
  margin-bottom: 1.875rem;
}
.footer__copyright {
  padding-top: 13px;
  border-top: 1px solid #dbdbdb;
  font-size: clamp(0.625rem, 0.474rem + 0.65vw, 1rem);
  display: flex;
  justify-content: center;
}
@media screen and (width < 600.99px) {
  .footer__inner {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .footer__title {
    pointer-events: all;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
  }
  .footer__title-drop {
    display: block;
    margin-left: 0.3125rem;
    transform: rotate(180deg);
    transition: transform 0.3s ease-in;
  }
  .footer__title.title--active .footer__title-drop {
    transform: rotate(0);
  }
  .footer__list {
    display: none;
  }
  .footer__copyright {
    flex-direction: column;
  }
}

.catalog__list {
  display: grid;
  grid-template-columns: repeat(auto-fit, 300px);
  gap: 1.875rem;
  grid-column: 4 span;
  justify-content: center;
  justify-items: center;
  margin: 0 auto;
}
@media screen and (width < 661.99px) {
  .catalog__list {
    grid-template-columns: repeat(2, 162px);
    grid-template-rows: repeat(2, 380px);
  }
}